import Vue from 'vue'
import Router from 'vue-router'
import RouterConfig from './router-config.js'

// const Foo = () => import(/* webpackChunkName: 'group-foo' */ './Foo.vue')
const HelloWorld = () => import(/* webpackChunkName: 'group-foo' */  '@/components/HelloWorld')
const Home = () => import(/* webpackChunkName: 'group-foo' */  '@/components/Home/Home')
const Signin = () => import(/* webpackChunkName: 'group-foo' */  '@/components/User/Signin')
const Signup = () => import(/* webpackChunkName: 'group-foo' */  '@/components/User/Signup')
const AboutThis = () => import(/* webpackChunkName: 'group-foo' */  '@/components/Javascript/AboutThis')
const Vuex = () => import(/* webpackChunkName: 'group-foo' */  '@/components/Javascript/Vuex')
const AliInterviewQues = () => import(/* webpackChunkName: 'group-foo' */  '@/components/InterviewQues/AliInterviewQues')

const MaxVuetifyHome = () => import(/* webpackChunkName: 'group-foo' */  '@/components/Youtube/MaxVuetify/Home')
const MaxVuetifyjs = () => import(/* webpackChunkName: 'group-foo' */  '@/components/Youtube/MaxVuetify/MaxVuetifyjs')
const Meetups = () => import(/* webpackChunkName: 'group-foo' */  '@/components/Youtube/MaxVuetify/Meetup/Meetups')
const CreateMeetup = () => import(/* webpackChunkName: 'group-foo' */  '@/components/Youtube/MaxVuetify/Meetup/CreateMeetup')
const Profile = () => import(/* webpackChunkName: 'group-foo' */  '@/components/Youtube/MaxVuetify/Profile')

Vue.use(Router)
const router = new Router({

  routes: [
    {
      path: '/',
      redirect: '/home',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/aboutThis',
      name: 'AboutThis',
      component: AboutThis
    },
    {
      path: '/vuex',
      name: 'Vuex',
      component: Vuex
    },
    {
      path: '/aliInterviewQues',
      name: 'AliInterviewQues',
      component: AliInterviewQues
    },
    {
      path: '/maxVuetifyjs',
      name: 'MaxVuetifyjs',
      redirect: '/maxVuetifyjs/maxVuetifyHome',
      component: MaxVuetifyjs,
      children: [
      	{
		      path: 'maxVuetifyHome',
		      name: 'MaxVuetifyHome',
		      component: MaxVuetifyHome
		    },
      	{
		      path: 'meetups',
		      name: 'Meetups',
		      component: Meetups
		    },
		    {
		      path: 'profile',
		      name: 'Profile',
		      component: Profile
		    },
		    {
		      path: 'createMeetup',
		      name: 'CreateMeetup',
		      component: CreateMeetup
		    }
      ]
    },
    
    {
      path: '/signIn',
      name: 'Signin',
      component: Signin
    },
    {
      path: '/signUp',
      name: 'Signup',
      component: Signup
    }
  ],
  scrollBehavior: function (to, from, savedPosition) {
  return savedPosition || { x: 0, y: 700 }
	}
	
})


export default router


